<template>
  <swiper class="home-swiper">
    <swiper-item v-for="item in cbanners" :key="item.image">
      <a :href="item.link">
        <img :src="item.image" alt="" @load="imgload">
      </a>
    </swiper-item>
  </swiper>
</template>

<script>
    import {Swiper, SwiperItem} from 'components/common/swiper';
    export default {
        name: "HomeSwiper",
        props: {
            cbanners:{
                type:Array,
                default() {
                    return []
                }
            }
        },
        data() {
          return {
            isLoad: false
          }
        },
        components:{
            Swiper,SwiperItem
        },
        methods: {
          imgload(){ 
            if(!this.isLoad){
              this.$emit('swiperImageLoad')
              this.isLoad = true
            }
          }
        },
    }
</script>

